.open-card {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 450px;
  height: 300px;
  background-color: #ffffff;
}


.open-card__stage {
  display: flex;
  padding: 8px 20px;
  align-items: center;
  justify-content: center;
  align-self: start;
  margin-left: 10%;
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  color: #ffd45c;
  background-color: #ffedc9;
  border-radius: 40px;
}

.open-card__title {
  padding: 30px 0px 20px 0px;
  font-family: "Roboto", sans-serif;
  font-size: 24px;
  font-weight: 700;
  color: #000000;
}


.open-card__comment {
  display: flex;
  padding: 10px 0px 10px 0px;
  justify-content: center;
  margin-bottom: 30px;
  width: 80%;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #000000;
  background-color: #f2f6fa;
  border-radius: 5px;
}


.open-card__author {
  display: flex;
  padding: 8px 25px 8px 25px;
  margin: 0px 0px 30px 10%;
  align-self: start;
  color: #82a3ff;
  border: 1px solid #e7e7e7;
  border-radius: 40px;
  background-color: #ffffff;
}

.open-card__author:hover {
 border: 1px solid #82a3ff;
 cursor: pointer;
}

.open-card__options {
  display: flex;
  justify-content: space-between;
  width: 80%;
  font-family: "Roboto", sans-serif; 
  font-size: 16px;
}


.open-card__changes {
  width: 110px;
  height: 30px;
  color: #dd8bf6;
  background-color: #fad9ff;
  border: none;
  border-radius: 5px;
}


.open-card__readiness {
  width: 110px;
  height: 30px;
  color: #b1a3f9;
  background-color: #ece6fe;
  border-radius: 5px;
  border: none;
}


.open-card__delete {
 width: 110px;
 height: 30px;
 color: #78f4d6;
 background-color: #c9fff2;
 border-radius: 5px;
 border: none;
}


.open-card__changes:hover {
  border: 1px solid #dd8bf6;
  cursor: pointer;
}

.open-card__readiness:hover {
  border: 1px solid #b1a3f9;
  cursor: pointer;
}

.open-card__delete:hover {
  border: 1px solid #78f4d6;
  cursor: pointer;
}


.open-card__close {
  position: absolute;
  right: 10px;
  top: 10px;
  width: 25px;
  height: 25px;
  opacity: 0.75;
}

.open-card__close:hover {
  opacity: 1;
}

.open-card__close:before,
.open-card__close:after {
  position: absolute;
  left: 15px;
  content: ' ';
  height: 25px;
  width: 2px;
  background-color: #82a3ff;
}

.open-card__close:before {
  transform: rotate(45deg);
}

.open-card__close:after {
  transform: rotate(-45deg);
}
