.card {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 450px;
    height: 300px;
    background-color: #ffffff;

    &__title {
        padding: 30px 0px 30px 0px;    
        font-family: "Roboto", sans-serif;
        font-size: 24px;
        font-weight: 700;
        color: #000000;
        letter-spacing: 0.5px;
    }

    &__avatar {
        display: flex;
        padding: 8px 25px 8px 25px;
        margin: 0px 0px 30px 10%;
        align-self: start;
        color: #82a3ff;
        border: 1px solid #e7e7e7;
        border-radius: 40px;
        background-color: #ffffff;
    }

    &__visit-time {
        position: absolute;
        left: 50px;
        bottom: 75px;
        font-family: "Roboto", sans-serif;
        font-size: 12px;
        font-weight: 500;
        color: #e7e7e7;
    }
}

.btn {
    padding: 1em 2em;
    margin: 1em;
    border: none;
    border-radius: 40px;
    font-size: 10px;
}

.card__btn--transfer {
    position: absolute;
    right: 70px;
    top: 13px;
    padding: 8px 20px;
    align-items: center;
    justify-content: center;
    align-self: start;
    margin-left: 10%;
    font-family: "Roboto", sans-serif;
    font-size: 18px;
    background-color: #ffedc9;
}

.card__btn--delete {
    position: absolute;
    right: 10px;
    top: 20px;
    opacity: 0.75;
}

.card__btn--add-user {
    position: absolute;
    left: 150px;
    bottom: 97px;
    opacity: 0.75;
    padding: 8px 20px;
}